@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,700;0,900;1,400;1,500;1,600;1,700;1,900&display=swap);
body {
  font-family: "Raleway", sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  height: 100vh;
  background-color: teal;
}

.expense-form {
  margin: 2rem 10rem;
  display: flex;
  height: 2.5rem;
  padding: 1.5rem;
  justify-content: space-evenly;
  background-color: black;
  border-radius: 2rem;
}

.expense-form input {
  font-size: large;
  border-radius: 1rem;
  border: none;
  outline: none;
  padding: 0.25rem 1rem;
}

.expense-form label {
  font-weight: 700;
  
}

.date-input,
.desc-input,
.cost-input {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

button,
.expense-btn {
  font-family: "Raleway", sans-serif;
  background-color: blue;
  color: white;
  font-weight: 700;
  border-radius: 10px;
  outline: none;
  border: none;
  padding: 5px 10px;
}

.expense-btn:hover {
  opacity: 0.7;
  transition: 0.2s ease;
}

.no-expenses {
  background-color: black;
  margin: 1.5rem 10rem;
  border-radius: 2rem;
  height: 70vh;
  display: grid;
  align-items: center;

}

@media only screen and (max-width: 1200px) {
  .no-expenses {
    margin: 1.5rem 1rem;
  }

  .expense-form {
    margin: 10px 1rem;
    display: flex;
    flex-direction: column;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
  }

  .expense-form input {
    margin: 1rem 0;
  }
}
